gen zugleich. ")]),_c('p',[_vm._v(" Trotzdem, es gibt auch Wege und Möglichkeiten mehr aus dem Beruf der medizinischen Fachangestellten zu machen und wieder glücklicher und zufriedener im Arbeitsalltag zu sein. ")])]),_c('b-col',{attrs:{"cols":"12","md":"6"}},[_c('p',[_vm._v(" MFA mal anders ist ein Netzwerk unter Gleichgesinnten. Um deinen beruflichen Weg im Gesundheitswesen zu finden und erfolgreich deine Ziele zu erreichen. Mit Fachwissen und persönlichen Erfahrungen stehen wir dir zur Seite und unterstützen dich – egal, ob es darum geht für dich die passende Fort- oder Weiterbildung zu finden, einen neuen Job zu finden, deine Gehaltswünsche durchzusetzen oder einfach nur durch den Praxisalltagswahnsinn zu helfen. ")])])],1),_c('h2',{staticClass:"my-4"},[_vm._v(" Wir helfen dir dabei einen Job zu finden, den du liebst. Und den du verdienst. ")]),_c('p',[_vm._v(" Viele Praxen haben zum Glück erkannt, dass ohne uns MFAs & ZFAs nichts läuft. Und das zeigen die Chefs in einem wertschätzenden Umgang, familienfreundlichen Arbeitsbedingungen und einer guten Bezahlung. In der Regel trennt sich die Spreu vom Weizen von ganz allein. Wir sind trotzdem sehr bemüht, bei den veröffentlichten Stellenanzeigen auf unserer Plattform einen besonderen Augenmerk auf bestimmte Kriterien zu legen: ")]),_c('ul',[_c('li',[_vm._v("Leistungsgerechte Bezahlung")]),_c('li',[_vm._v(" Tätigkeiten, die du mit einer Ausbildung zur ArzthelferIn (Medizinischen & Zahnmedizinischen Fachangestellten) ausüben kannst – auch wenn der ausgeschriebene Job nicht immer der typische ArzthelferIn-Job ist ")]),_c('li',[_vm._v(" Unbefristete Stellen oder befristete Stellen mit Aussicht auf Übernahme ")]),_c('li',[_vm._v(" Keine Jobs über Zeitarbeitsfirmen oder Personaldienstleiter ")])]),_c('div',{staticClass:"d-flex justify-content-center my-5"},[_c('ToJobboardBanner')],1),_c('h2',{staticClass:"my-4"},[_vm._v("Bock auf Veränderung?")]),_c('p',[_vm._v(" Als MFA & ZFA hast du einen anspruchsvollen Job in der Arz