 für den Patienten nicht ersichtlich sind. Der Beruf vereint bürokratische, organisatorische und medizinische Aufgaben. ")]),_c('b-row',{attrs:{"cols":"12","lg":"4"}},[_c('b-col',[_c('div',{staticClass:"example"},[_c('h3',{staticClass:"h5"},[_vm._v(" Beispiele für medizinische Tätigkeiten: ")]),_c('p',{staticClass:"mb-0"},[_vm._v(" Sie assistieren den Ärzten bei Untersuchungen oder chirurgischen Eingriffen und bereiten Untersuchungsräume entsprechend vor und nach. Sie legen Verbände an. Manche MFAs arbeiten im praxiseigenen Labor, nehmen Blut ab und führen teilweise selber Blut- und Urinuntersuchungen durch. Sie verabreichen Injektionen, Impfungen und Infusionen. Nicht zuletzt ist die MFA auch für die fachgerechte Durchführung der Desinfektion von Arbeitsflächen und Instrumenten zuständig. ")])])]),_c('b-col',[_c('div',{staticClass:"example"},[_c('h3',{staticClass:"h5"},[_vm._v(" Beispiele für bürokratische Tätigkeiten: ")]),_c('p',{staticClass:"mb-0"},[_vm._v(" Von Tätigkeiten an der Rezeption, Rezepte erstellen, Anträge für Krankenkassen oder Versorgungskassen ausfüllen, Bestellungen und den Einkauf der Praxis zu managen, erledigen Medizinische Fachangestellte ebenfalls die Abrechnung und etablieren ein funktionierendes Qualitätsmanagement. ")])])]),_c('b-col',[_c('div',{staticClass:"example"},[_c('h3',{staticClass:"h5"},[_vm._v(" Beispiele für organisatorische Tätigkeiten: ")]),_c('p',{staticClass:"mb-0"},[_vm._v(" Medizinische Fachangestellte sorgen für einen reibungslosen Sprechstundenablauf. Sie vereinbaren Termine so, dass Patienten nicht zu lange warten. Koordinieren Untersuchungen, sodass entsprechende Räume und Kollegen zur Verfügung stehen. Notfälle am Telefon oder an der Rezeption erkennen sie schnell und können fachgerecht handeln. ")])])])],1),_c('h2',[_vm._v(" Welche Fähigkeiten muss ich als Medizinische Fachangestellte haben? ")]),_c('p',[_vm._v(" Als Medizinische Fachangestellte muss man nicht perfekt sein, aber man ist schon ziemlich nah dran 😉. Um den Praxisalltag mit all seinen Herausforderungen meistern zu können und diesen wirklich wunderbaren Beruf mit Freude auszuüben, helfen bestimmte Eigenschaften: ")]),_c('b-row',{attrs:{"cols":"1","cols-lg":"2"}},[_c('b-col',[_c('ul',[_c('li',[_vm._v("Organisationstalent")]),_c('li',[_vm._v("Gutes Zeitmanagement")]),_c('li',[_vm._v("Multitaskingfähig")]),_c('li',[_vm._v("Freude am Umgang mit Menschen")]),_c('li',[_vm._v("Gute Kommunikationsfähigkeit")]),_c('li',[_vm._v("Einfühlungsvermögen")]),_c('li',[_vm._v(" Verschwiegenheit (Patientengeheimnis und Schweigepflicht) ")]),_c('li',[_vm._v("Interesse an der Medizin")]),_c('li',[_vm._v(" Keine Scheu vor dem menschlichen Körper, seinen Sekreten bzw. Ausscheídungen und Gerüchen ")]),_c('li',[_vm._v("Gewissenhaftes arbeiten")]),_c('li',[_vm._v("Verantwortungsbewusstsein")]),_c('li',[_vm._v("Genauigkeit")]),_c('li',[_vm._v(" Eine gewisse Serviceorientiertheit (der Patient ist schließlich König 🤷‍♀️) ")])])]),_c('b-col',{staticClass:"d-flex justify-content-center align-items-center"},[_c('b-img',{staticClass:"my-4",staticStyle:{"max-width":"80%"},attrs:{"fluid":"","src":require("@/assets/img/profile_mfa.svg")}})],1)],1),_c('h2',[_vm._v(" Wie läuft die Ausbildung zur Medizinischen Fachangestellten ab? ")]),_c('p',[_vm._v(" Die Ausbildung zur MFA findet berufsbegleitend statt. Das heißt, praktische Kenntnisse werden in der Ausbildungspraxis vermittelt und theoretisches Wissen erlernst du in der Berufsschule. ")]),_c('p',[_vm._v(" Zur Berufsschule gehst du, je nach Bundesland, 1-2 mal die Woche. In der Berufsschule lernst du alles, was du im Berufsalltag benötigst. Dazu zählen medizinische Inhalte, wie die Lehre der Anatomie, Pathologie und Physiologie, um den Körper, seine normalen Funktionen und Krankheiten zu verstehen. Daneben behandelt ihr verwaltungstechnischen Lehrstoff über Abrechnung, Datenschutz und das deutsche Gesundheitssystem. Aber auch interessante Dinge zu Hygienestandards, Kommunikation mit Patienten und Qualitätsmanagement werden in der Berufsschule erlernt. ")]),_c('p',[_vm._v(" Insgesamt dauert die Ausbildung zur MFA drei Jahre. ")]),_c('p',[_vm._v(" Die Inhalte der Ausbildung sind in einem sogenannten Ausbildungsrahmenplan in der "),_c('b-link',{attrs:{"href":"https://www.bundesaerztekammer.de/fileadmin/user_upload/downloads/MFA_Verordnung_05052006.pdf","target":"_blank"}},[_vm._v("Verordnung über die Berufsausbildung zum Medizinischen Fachangestellten / zur Medizinischen Fachangestellten")]),_vm._v(" verankert. Der Rahmenplan legt fest, welche Inhalte in welchem Ausbildungsjahr vermittelt werden müssen. Die Ausbildungspraxis schreibt einen Ausbildungsplan nieder, an dem sich die Azubis orientieren können. ")],1),_c('h3',[_vm._v("Inhalte der MFA Ausbildung")]),_c('div',{staticClass:"example"},[_c('h3',{staticClass:"h5"},[_vm._v(" 1. Ausbildungsjahr ")]),_c('p',{staticClass:"mb-0"},[_vm._v(" Im ersten Ausbildungsjahr erlernt man zunächst viele Grundlagen, z.B. zum Arbeitsrecht und Datenschutz. Aber auch den richtigen Umgang mit Patienten und wie du bei Untersuchungen oder Therapien richtig assisierst. Oft ist hier noch ein/e erfahrene/r KollegIn dabei, um dir zu helfen. ")])]),_c('div',{staticClass:"example"},[_c('h3',{staticClass:"h5"},[_vm._v(" 2. Ausbildungsjahr ")]),_c('p',{staticClass:"mb-0"},[_vm._v(" Im zweiten Lehrjahr kommen Aufgabenfelder aus dem Bereich des Praxismanagements hinzu, wie Abrechnung oder Qualitätsmanagement. Die Azubis bekommen außerdem verantwortungsvollere Aufgaben zugeteilt und können jetzt Untersuchungsräume selbst vorbereiten, Instrumente aufbereiten oder Patienten an der Rezeption eigenständig empfangen und entlassen. ")])]),_c('div',{staticClass:"example"},[_c('h3',{staticClass:"h5"},[_vm._v(" 3. Ausbildungsjahr ")]),_c('p',{staticClass:"mb-0"},[_vm._v(" Im dritten Lehrjahr kommt das große Themenfeld der Prävention auf den Unterrichtsplan. Die Azubis lernen, wie Krankheiten vermieden werden und die Gesundheit gefördert wird. MFAs belgleiten in ihrem Praxisalltag den Patienten oft dabei, indem sie ihn beraten und unterstützen. Verantwortungsvolle Tätigkeiten am Patienten, wie das Blutabnehmen oder Fäden ziehen, runden die Ausbildung ab. ")])]),_c('div',{staticClass:"example"},[_c('h3',{staticClass:"h5"},[_vm._v(" Berichtsheft ")]),_c('p',{staticClass:"mb-0"},[_vm._v(" Während der gesamten Ausbildungszeit müssen die Azubis ein Berichtsheft pflegen und ihre regelmäßigen Aufgaben sowie das Erlernte, praktisch wie theoretisch, schriftlich festhalten. Der Ausbilder, in der Regel der Praxisinhaber, ist verpflichtet die angefertigten Berichte zu überwachen und mit seiner Unterschrift zu bestätigen. Das Berichtsheft kann man also als eine Art Ausbildungsnachweis ansehen und ist Voraussetzung, um die Ausbildung abschließen zu können und zur Abschlussprüfung zugelassen zu werden. ")])]),_c('h2',[_vm._v(" Zwischen- und Abschlussprüfung ")]),_c('p',[_vm._v(" Am Ende des zweiten Ausbildungsjahres findet eine Zwischenprüfung statt, um den Wissensstand der Auszubildenden abzufragen. Die Prüfung ist schriftlich und dauert 120 Minuten. Dabei werden alle Themenfelder abgefragt, die die Azubis bis zu diesem Zeitpunkt in der Berufsschule kennengelernt haben. ")]),_c('p',[_vm._v(" Um zur Abschlussprüfung zugelassen zu werden, müssen die Azubis an der Zwischenprüfung teilgenommen haben und ihr Berichtsheft bei der Ärztekammer eingereicht haben. ")]),_c('p',[_vm._v(" Die Abschlussprüfung wird am Ende des dritten Ausbildungsjahres absolviert und gliedert sich in einen schriftlichen und einen praktischen Prüfungsteil. Die schriftlichen Klausuren umfassen insgesamt 300 Minuten. ")]),_c('p',[_vm._v(" Bei der praktischen Prüfung ziehen die Auszubildenden in der Regel aus mehreren Umschlägen ihre Prüfungsaufgabe. Insgesamt dauert die praktische Prüfung 75 Minuten. In dieser Zeit bearbeiten die Auszubildenden ihre Prüfungsaufgabe und bereiten sich auf das Prüfungsgespräch mit den Prüfern vor. Die Auszubildenden spielen dabei eine typische Situation aus dem Praxisalltag nach, z.B. wie eine MFA einen chirurgischen Tisch vorbereitet. Danach erfolgt ein abschließendes Fachgespräch von maximal 15 Minuten, indem die Auszubildenden ihr Vorgehen erklären und einige Fachfragen zu der Thematik beantworten. ")]),_c('p',[_vm._v(" Bei erfolgreich absolvierter schriftlicher und praktischer Prüfung mit einer Mindestnote von “ausreichend” ist die Ausbildung abgeschlossen. Die Auszubildenden erhalten einen Ausbildungsbrief zur/zum Medizinischen Fachangestellten. ")]),_c('h2',[_vm._v(" Wie hoch ist die Ausbildungsvergütung für Medizinische Fachangestellte? ")]),_c('p',[_vm._v(" Der "),_c('b-link',{attrs:{"href":"https://www.vmf-online.de/mfa/mfa-tarife","target":"_blank"}},[_vm._v("Verband Medizinischer Fachberufe e.V. (VMF)")]),_vm._v(" ist die einzige Organisation für Medizinische Fachangestellte auf Arbeitnehmerseite. Sie vertritt uns Medizinische Fachangestellte bei Tarifverhandlungen gegenüber der Arbeitsgemeinschaft zur Regelung der Arbeitsbedingungen der ArzthelferInnen / Medizinischen Fachangestellten (AAA). Die AAA ist die Tarifpartei der Arbeitgeber, sprich der niedergelassenen Ärzte. ")],1),_c('p',[_vm._v(" Der VMF setzt sich auch für die Ausbildungsvergütung der MFA ein und hat bei der letzten Tarifverhandlung sehr gute Ergebnisse für die Gehälter und Ausbildungsvergütungen erzielt. In den kommenden drei Jahren wird sich die Ausbildungsvergütung von Medizinischen Fachangestellten stetig erhöhen. Wahnsinn, wenn ich daran denke, dass ich zum Start meiner Ausbildung im Jahr 2004 gerade einmal 480 Euro verdient habe. Glücklicherweise konnte die Vergütung mittlerweile fast verdoppelt werden. ")]),_c('div',{staticClass:"my-5"},[_c('b-table-simple',{attrs:{"hover":"","responsive":"","table-variant":"secondary"}},[_c('colgroup',[_c('col')]),_c('colgroup',[_c('col'),_c('col'),_c('col')]),_c('b-thead',{attrs:{"head-variant":"primary"}},[_c('b-tr',[_c('b-th',{attrs:{"colspan":"1"}}),_c('b-th',{staticClas