st. Beziehe dich hierbei am besten auf Projekte und Aufgabengebieten, die zur Stelle passen. Zum Beispiel, dass du für das Impfmanagement verantwortlich warst und ihr die durchschnittliche Impfquote in der Praxis durch dein Engagement deutlich erhöhen konntet. ")]),_c('h3',{staticClass:"h5 bold"},[_vm._v("3. Motivation und Ziele")]),_c('p',[_vm._v(" Im letzten Absatz beschreibst du, was dich motiviert bei dem ausgewählten Unternehmen zu arbeiten. Warum möchtest du ausgerechnet in der Praxis oder der Firma arbeiten? Bessere Entwicklungsmöglichkeiten? Kleineres, familiäreres Team? ")]),_c('h2',{staticClass:"mb-4"},[_vm._v("Der Lebenslauf")]),_c('p',[_vm._v(" Achte auch hier auf eine übersichtliche Darstellung und passe das Design farblich dem Anschreiben an. ")]),_c('h3',{staticClass:"h5 bold"},[_vm._v("Zum Aufbau")]),_c('b-img',{staticClass:"p-2",staticStyle:{"width":"50vw","max-width":"350px"},attrs:{"right":"","src":require("@/assets/img/personal_data__monochromatic.svg"),"alt":"Grafik von einem Lebenslauf"}}),_c('p',[_vm._v(" Liste deine beruflichen Stationen auf und schreibe immer 2-3 Beispiele zu den Aufgaben, die du bei den einzelnen Arbeitgebern hattest. Das macht deinen Lebenslauf etwas lebendiger und hebt dich von anderen Bewerbern ab. ")]),_c('p',[_vm._v(" Deinen Bildungsweg kannst du ganz klassisch mit Name der Schule/ Einrichtung, Dauer und dem erworbenen Abschluss aufführen. Bei guten und sehr guten Noten erwähne deine Abschlussnote. Man darf ruhig ein bisschen prahlen 😊. ")]),_c('p',[_vm._v(" Gut ist immer, wenn du zusätzliche Qualifikationen erworben hast, die dich im Berufsleben weiterbringen. Diese solltest du unbedingt erwähnen. Dazu zählen z. B. Weiterbildungen, Fortbildungen, ein berufsbegleitendes Studium oder auch erworbene Kenntnisse, die du dir privat angeeignet hast. ")]),_c('p',[_vm._v(" In der Mustervorlage findest du im Lebenslauf einen kleinen Abschnitt „Über mich“. Beschreibe dich in einem Satz oder stichpunktartig. Beziehe dich hier am 